<template>
  <div class="cy_menu" ref="cy_menu">
    <div class="cy_menu_con">
        <dl class="menu">
            <dt>基础类libs</dt>
            <dd>
              <a href="#/">窗口综合示例</a>
            </dd>
            <dd>
              <a href="#/sliderTab">滑动tab标签<span class="right">sliderTab</span></a>
            </dd>
            <dd>
              <a href="#/sliderWin">窗口左右切换<span class="right">sliderWin</span></a>
            </dd>
            <dd>
              <a href="#/sliderScroll">窗口上下滚动<span class="right">sliderScroll</span></a>
            </dd>
            <dd>
              <a href="#/slider">图片滑动轮播<span class="right">slider</span></a>
            </dd>
            <dd>
              <a href="#/area">菜单联动效果<span class="right">area</span></a>
            </dd>
            <dd>
              <a href="#/sliderRange">滑块范围选择<span class="right">sliderRange</span></a>
            </dd>
            <dd>
              <a href="#/tooltip">提示层<span class="right">tooltip</span></a>
            </dd>
            <dd>
              <a href="#/iterator">迭代器加载模块<span class="right">iterator</span></a>
            </dd>
            <dd>
              <a href="#/valid">数据校验1<span class="right">valid</span></a>
            </dd>
            <dd>
              <a href="#/validate">数据校验2<span class="right">validate</span></a>
            </dd>
            <dd>
              <a href="#/ajax">ajax请求数据<span class="right">ajax</span></a>
            </dd>
            <dd>
              <a href="#/jsonp">jsonp请求数据<span class="right">jsonp</span></a>
            </dd>
            <dd>
              <a href="#/ui">遮罩层&amp;弹出窗口<span class="right">ui</span></a>
            </dd>
            <dd>
              <a href="#/offset">获取元素位置信息<span class="right">offset</span></a>
            </dd>
            <dd>
              <a href="#/cookie">cookie<span class="right">cookie</span></a>
            </dd>
            <dd>
              <a href="#/cache">数据缓存<span class="right">cache</span></a>
            </dd>
            <dd>
              <a href="#/countdown">倒计时<span class="right">countdown</span></a>
            </dd>
            <dd>
              <a href="#/browser">浏览器判断<span class="right">browser</span></a>
            </dd>            
        </dl>
        <dl class="menu">
            <dt>业务组件</dt>
            <dd>
              <a href="#/ajaxUtil">ajaxUtil方法<span class="right">ajaxUtil</span></a>
            </dd>
            <dd>
              <a href="#/nativeUtil">APP交互<span class="right">nativeUtil</span></a>
            </dd>
            <dd>
              <a href="#/userUtil">用户登录态<span class="right">userUtil</span></a>
            </dd>
            <dd>
              <a href="#/shareUtil">分享方法<span class="right">shareUtil</span></a>
            </dd>
            <dd>
              <a href="#/serverUtil">常用服务类<span class="right">serverUtil</span></a>
            </dd>
            <dd>
              <a href="#/funcModule">功能球<span class="right">funcModule</span></a>
            </dd>
            <dd>
              <a href="#/adModule">广告推荐位<span class="right">adModule</span></a>
            </dd>
            <dd>
              <a href="#/adChModule">品型广告位<span class="right">adChModule</span></a>
            </dd>

        </dl>         
    </div>
  </div>
</template>

<script>
  import sliderScroll from 'cy_static/sliderScroll';
  export default {
    name: 'cymenu',
    data() {
      return {}
    },
    mounted() {
        let m = this;
        var hash = location.hash;
        m.$refs.cy_menu.querySelectorAll("a").forEach(element=>{
            if (element.getAttribute("href") == hash) {
                element.parentNode.classList.add("curr");
            }
        });
        m.initOutWin();
    },
    methods: {          
      initOutWin : function () {
          var m = this, myMember = document.querySelector('.cy_menu');   
          if (m.outWin) return;             
          m.outWin = sliderScroll({
              elem: myMember,// 窗口元素
              content: ".cy_menu_con",
              isTopEffect: false,
              isBotEffect: false,
              hasMask:true,
              hasHoveScrollbar: true,
              pushDown: {}// 底部加载
          });
          m.outWin.isEndPull(true);
          m.outWin.isEndPush(true);
      }
    },
    activated () {
        this.outWin.setActivated(true);
    },
    deactivated(){
        this.outWin.setActivated(false);
    },
    beforeDestroy() {
        this.outWin.destroy();
    }
  }
</script>

<style scoped>
</style>
 



