import Vue from 'vue'
import Router from 'vue-router'
const winDemo = r => require.ensure([], () => r(require('../view/libs/winDemo')), 'WinDemo')
const winDemoExample = r => require.ensure([], () => r(require('../view/libs/winDemo/example')), 'WinDemoExample')
const runcode = r => require.ensure([], () => r(require('../view/runcode')), 'Runcode')
const sliderTab = r => require.ensure([], () => r(require('../view/libs/sliderTab')), 'SliderTab')
const sliderTabExample = r => require.ensure([], () => r(require('../view/libs/sliderTab/example')), 'SliderTabExample')
const sliderWin = r => require.ensure([], () => r(require('../view/libs/sliderWin')), 'SliderWin')
const sliderWinExample = r => require.ensure([], () => r(require('../view/libs/sliderWin/example')), 'SliderWinExample')
const sliderScroll = r => require.ensure([], () => r(require('../view/libs/sliderScroll')), 'SliderScroll')
const sliderScrollExample = r => require.ensure([], () => r(require('../view/libs/sliderScroll/example')), 'SliderScrollExample')
const slider = r => require.ensure([], () => r(require('../view/libs/slider')), 'Slider')
const sliderExample = r => require.ensure([], () => r(require('../view/libs/slider/example')), 'SliderExample')
const area = r => require.ensure([], () => r(require('../view/libs/area')), 'Area')
const areaExample1 = r => require.ensure([], () => r(require('../view/libs/area/example1')), 'AreaExample1')
const areaExample2 = r => require.ensure([], () => r(require('../view/libs/area/example2')), 'AreaExample2')
const sliderRange = r => require.ensure([], () => r(require('../view/libs/sliderRange')), 'SliderRange')
const sliderRangeExample = r => require.ensure([], () => r(require('../view/libs/sliderRange/example')), 'SliderRangeExample')
const tooltip = r => require.ensure([], () => r(require('../view/libs/tooltip')), 'Tooltip')
const iterator = r => require.ensure([], () => r(require('../view/libs/iterator')), 'Iterator')
const valid = r => require.ensure([], () => r(require('../view/libs/valid')), 'Valid')
const validate = r => require.ensure([], () => r(require('../view/libs/validate')), 'Validate')
const ajax = r => require.ensure([], () => r(require('../view/libs/ajax')), 'Vjax')
const jsonp = r => require.ensure([], () => r(require('../view/libs/jsonp')), 'Jsonp')
const ui = r => require.ensure([], () => r(require('../view/libs/ui')), 'Ui')
const offset = r => require.ensure([], () => r(require('../view/libs/offset')), 'Offset')
const cookie = r => require.ensure([], () => r(require('../view/libs/cookie')), 'Cookie')
const cache = r => require.ensure([], () => r(require('../view/libs/cache')), 'Cache')
const countdown = r => require.ensure([], () => r(require('../view/libs/countdown')), 'Countdown')
const browser = r => require.ensure([], () => r(require('../view/libs/browser')), 'Browser')

const ajaxUtil = r => require.ensure([], () => r(require('../view/utils/ajaxUtil')), 'AjaxUtil')
const nativeUtil = r => require.ensure([], () => r(require('../view/utils/nativeUtil')), 'NativeUtil')
const userUtil = r => require.ensure([], () => r(require('../view/utils/userUtil')), 'UserUtil')
const shareUtil = r => require.ensure([], () => r(require('../view/utils/shareUtil')), 'ShareUtil')
const serverUtil = r => require.ensure([], () => r(require('../view/utils/serverUtil')), 'ServerUtil')
const funcModule = r => require.ensure([], () => r(require('../view/utils/funcModule')), 'FuncModule')
const funcModuleExample = r => require.ensure([], () => r(require('../view/utils/funcModule/example')), 'FuncModuleExample')
const adModule = r => require.ensure([], () => r(require('../view/utils/adModule')), 'AdModule')
const adModuleExample = r => require.ensure([], () => r(require('../view/utils/adModule/example')), 'AdModuleExample')
const adChModule = r => require.ensure([], () => r(require('../view/utils/adChModule')), 'AdChModule')
const adChModuleExample = r => require.ensure([], () => r(require('../view/utils/adChModule/example')), 'AdChModuleExample')

const develop = r => require.ensure([], () => r(require('../view/develop/index')), 'Develop')
const spm = r => require.ensure([], () => r(require('../view/spm/index')), 'Spm')



Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        name: 'WinDemo',
        component: winDemo
    }, {
        path: '/winDemoExample',
        name: 'WinDemoExample',
        component: winDemoExample
    }, {
        path: '/runcode',
        name: 'Runcode',
        component: runcode
    }, {
        path: '/sliderTab',
        name: 'SliderTab',
        component: sliderTab
    }, {
        path: '/sliderTabExample',
        name: 'SliderTabExample',
        component: sliderTabExample
    },{
        path: '/sliderWin',
        name: 'SliderWin',
        component: sliderWin
    }, {
        path: '/SliderWinExample',
        name: 'SliderWinExample',
        component: sliderWinExample
    },{
        path: '/sliderScroll',
        name: 'SliderScroll',
        component: sliderScroll
    }, {
        path: '/sliderScrollExample',
        name: 'SliderScrollExample',
        component: sliderScrollExample
    }, {
        path: '/slider',
        name: 'Slider',
        component: slider
    }, {
        path: '/sliderExample',
        name: 'SliderExample',
        component: sliderExample
    }, {
        path: '/area',
        name: 'Area',
        component: area
    }, {
        path: '/areaExample1',
        name: 'AreaExample1',
        component: areaExample1
    }, {
        path: '/areaExample2',
        name: 'AreaExample2',
        component: areaExample2
    }, {
        path: '/tooltip',
        name: 'Tooltip',
        component: tooltip
    }, {
        path: '/sliderRange',
        name: 'SliderRange',
        component: sliderRange
    }, {
        path: '/sliderRangeExample',
        name: 'SliderRangeExample',
        component: sliderRangeExample
    },{
        path: '/iterator',
        name: 'Iterator',
        component: iterator
    },{
        path: '/valid',
        name: 'Valid',
        component: valid
    },{
        path: '/validate',
        name: 'Validate',
        component: validate
    },{
        path: '/ajax',
        name: 'Ajax',
        component: ajax
    },{
        path: '/jsonp',
        name: 'Jsonp',
        component: jsonp
    },{
        path: '/ui',
        name: 'Ui',
        component: ui
    },{
        path: '/offset',
        name: 'Offset',
        component: offset
    },{
        path: '/cookie',
        name: 'Cookie',
        component: cookie
    },{
        path: '/cache',
        name: 'Cache',
        component: cache
    },{
        path: '/countdown',
        name: 'Countdown',
        component: countdown
    }, {
        path: '/browser',
        name: 'Browser',
        component: browser
    }, {
        path: '/ajaxUtil',
        name: 'AjaxUtil',
        component: ajaxUtil
    }, {
        path: '/funcModule',
        name: 'FuncModule',
        component: funcModule
    }, {
        path: '/funcModuleExample',
        name: 'FuncModuleExample',
        component: funcModuleExample
    }, {
        path: '/adModuleExample',
        name: 'AdModuleExample',
        component: adModuleExample
    }, {
        path: '/adModule',
        name: 'AdModule',
        component: adModule
    }, {
        path: '/adChModule',
        name: 'AdChModule',
        component: adChModule
    }, {
        path: '/adChModuleExample',
        name: 'AdChModuleExample',
        component: adChModuleExample
    }, {
        path: '/nativeUtil',
        name: 'NativeUtil',
        component: nativeUtil
    }, {
        path: '/userUtil',
        name: 'UserUtil',
        component: userUtil
    }, {
        path: '/shareUtil',
        name: 'ShareUtil',
        component: shareUtil
    }, {
        path: '/serverUtil',
        name: 'ServerUtil',
        component: serverUtil
    }, {
        path: '/develop',
        name: 'Develop',
        component: develop
    }, {
        path: '/spm',
        name: 'Spm',
        component: spm
    }]
})




